<template>
    <div v-if="ready">
        <div class="card col-12 px0" style='height:auto'>
            <div class="card-header">
                <strong>Group Settings</strong>
            </div>            
            <div class="card-body card-block">
                 <label><u class='description'>Gorup List</u></label>
                 <div class="col-4 pl0" style='margin-bottom:0.75rem;'>
                        <select class="form-control select" v-model="section" @change="fetch_list">
                            <option value='Secondary'>Secondary Section</option>
                            <option value='Primary'>Primary Section</option>
                            <option value='Kindergarden'>Kindergarden Section</option>
                        </select>
                    </div>
                    <v-data-table
                        :headers="headers"
                        :items="grouplist"
                        class="elevation-1"
                        :rows-per-page-items="[5]"
                    >
                        <template v-slot:items="props">
                            <td><a href="" v-on:click.prevent="remove_group(props.item.groupcode)">Remove</a></td>
                            <td>{{ props.item.group_description }}</td>
                            <td>{{ props.item.groupcode }}</td> 
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>        
                        </template>
                    </v-data-table><br/>
                    <label><u class='description'>Create New Group</u></label>
                    <form class="col-12 px0 flex" @submit.prevent="insertgroup">
                        <div class="col-6 pl0">
                            <input type="text" class="form-control" placeholder="Enter Group Description" required v-model="grp_dp"/>
                        </div>
                        <div class="col-4">
                            <input type="text" class="form-control" placeholder="Enter Group Code" required v-model="grp_code"/>
                        </div>
                        <div class="col-2 pr0">
                            <button type="submit" class="form-control">Insert</button>
                        </div>
                    </form>
            </div>
            <div class="card-footer">
                <button type="button" class="btn btn-danger btn-sm" @click="return_prev">
                    <i class="fa fa-undo"></i> Return
                </button>
            </div>
        </div>
    </div>    
</template>
<script>
export default {
    data(){
        return{
            grp_dp:'',
            grp_code:'',
            ready:false,
            section:'Secondary',
            grouplist:[],
            headers: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Name',value:'section',sortable: false},
                { text: 'Code',value:'groupcode',sortable: false},
            ], 
        }
    },
    async created(){
        await this.fetch_list();
        this.ready=true;
    },
    async mounted(){         
        
    },
    methods:{
        async fetch_list(){
            var formData = new FormData();
            formData.append('section',this.section);
            await axios.post('inventory/web/backend/fetch_grp_code.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.grouplist = response.data;
                        } else {    
                            this.grouplist = [];
                        }
                    }
                )
        },
        async insertgroup(){
            var formData = new FormData();
            formData.append('grp_dp',this.grp_dp);
            formData.append('grp_code',this.grp_code);
            formData.append('section',this.section);
            await axios.post('inventory/web/backend/insert_grp.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.$alert("Successfully inserted group").then(
                                ()=>{
                                    this.fetch_list();
                                this.grp_dp='';
                                this.grp_code='';
                                }
                            )                           
                        } else {
                            this.$alert(response.data);
                        }
                    }
                )
        },
        async remove_group(code){
            var formData = new FormData();
            formData.append('code',code);
            var msg = "Confirm deleting group code : "+code+' ?';
            this.$confirm(msg).then(
                async()=>{
                     await axios.post("inventory/web/backend/remove_grp.php",formData)
                        .then(
                            (response)=>{
                                if(response.data=='OK'){
                                    this.$alert("Successfully deleted group").then(
                                        ()=>{
                                            this.fetch_list();
                                        }
                                    )
                                } else {
                                    this.$alert(response.data);
                                }
                            }
                        )
                }
            )           
        },
        return_prev(){
            this.$router.go(-1);
        }
    }
}
</script>
<style scoped>
.px0{
    padding-left:0;
    padding-right:0;
}
.pl0{
    padding-left:0;
}
.pr0{
    padding-right:0;
}
.description{
    font-weight: 600;
}
</style>


